import Vue from 'vue'
import App from './App'
import router from './router'
import {store} from './store/store'
import {mapActions} from 'vuex'

Vue.config.productionTip = false
export const urlstart = 'https://api.github.com/search/repositories?q=stars:%3E1+language:'
export const urlend = '&sort=stars&order=desc&type=Repositories'

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



