<template>
  <div>
    <TitleAndSearch />
    <div class="loader" v-if="this.$store.getters.showProgressSpinner"/>
    <CardHolder />
</div>
</template>

<script>
import TitleAndSearch from './TitleAndSearch.vue'
import {urlstart, urlend} from '../main.js'
import CardHolder from './CardHolder.vue'
import {mapActions} from 'vuex'
export default {
  name: 'GithubTrending',
  methods: {
    ...mapActions([
      'getResponse'
    ]),
  },
  mounted(){
    this.getResponse()
  },
  components: {
    TitleAndSearch,
    CardHolder
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.loader{
  display: inline-block;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-top: -60px;
  margin-left: -60px;
  border: 16px solid #f3f3f3;
  border-radius: 50%;
  border-top: 16px solid #3498db;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite; /* Safari */
  animation: spin 2s linear infinite;
}
</style>



